.plan-bg {
    padding: 45px 86px;
    margin: 0 14px;
}
.plan-bg_tariff {
    margin: 0 70px;
    padding: 25px 43px;
}
.plan-bg_style {
    margin-top: 39px;
}

.plan {
    border-radius: 28px;
    border-width: 2px;
    padding: 3px;
}
.plan__item {
    height: 48px;
    line-height: 48px;
    cursor: pointer;
    transition: .3s;
}
.plan__item_active {
    background-color: #dd1781 !important;
    color: #fff !important;
}

.plan_red {
    border-color: #fff;
}
.plan_red .plan__item {
    color: #fff;
}
.plan_red .plan__item_active {
    background-color: #fff !important;
    color: #212121 !important;
}



.plan_class .plan__item {
    width: 175px;
    font-size: 15px;
}
.plan_class-gray .plan__item {
    background-color: #e7e7e7;
}
.plan_class-bright .plan__item {
    background-color: #e7e7e730;
}
.plan_class-bright .plan__item_active {
    color: #dd1781 !important;
}
.plan_class .plan__item:not(:last-child) {
    margin-right: 2px;
}


.plan_tariff .plan__item {
    width: 85px;
    line-height: 30px;
    height: 30px;
    font-size: 14px;
}
