.checkboxes_standard {
    margin-top: 103px;
    margin-bottom: 129px;
}
.checkboxes__1 {
    max-width: 45%;
    padding: 38px 38px 28px 28px;
}
.checkboxes__2 {
    background-image: $pinkToPurple;
    padding: 38px 38px 28px 28px;
    min-width: 55%;
}
.checkboxes__2 .checkboxes__line:first-child,
.checkboxes__1 .checkboxes__line:first-child {
    margin-bottom: 30px;
}
.checkboxes__line label:first-child {
    margin-right: 35px;
}

