@media screen and (max-width: 1600px) {
    .container {
        margin-right: 75px;
    }
    .col-xl_1 {
        display: none;
    }
    .table-wrapper-1, .table-wrapper_2 {
        justify-content: flex-start;
    }
}
@media screen and (max-width: 1500px) {
    .left-menu {
        display: none;
    }
    .container {
        margin: auto;
    }
}
@media screen and (max-width: 1199px) {
    .buttons_block {
        padding-right: 5px;
        
    }
    .buttons__big .button:last-child {
        margin-top: 10px;
    }
    .buttons__big .button {
        margin-right: 28px;
    }
    .buttons__text {
        margin-top: 28px;
    }


    .table-1,
    .table-wrapper-2 {
        overflow-x: auto;
    }

    .plan-bg {
        margin: 0 !important;
        margin-left: -22px !important;
    }

    .table, .table__title {
        width: 731px;
    }
    .thead .table__col:first-child {
        width: 300px;
    }


    .instagram__iphone {
        display: none;
    }
}
@media screen and (max-width: 991px) {

    .page-snippets {
        width: 679px;
        margin: 0;
    }
    .colors-wrapper {
        position: relative;
        left: 44px;
    }
}
@media screen and (max-width: 884px) {
    .colors-wrapper {
        left: 20px;
    }
}
@media screen and (max-width: 767px) {
    .footer {
        text-align: center;
    }
    .socials__text {
        display: none;
    }
    .footer__rights {
        margin-top: 23px;
    }

    .ctr-wrapper {
        width: 100% !important;
        margin: 0;
    }
    .ctr__text {
        max-width: 245px;
    }


    .table td {
        line-height: 48px !important;
        height: 48px !important;
    }

    .table td, .table th {
        padding-top: 0;
        padding-bottom: 0;
    }
    .forms-wrapper {
        width: 100% !important;
    }
    .form__message {
        width: 100% !important;
        margin: 0;
        margin-top: 15px;
    }
    .bg_light-wrapper {
        padding-left: 15px;
        padding-right: 15px;
    }
    .bg_form {
        padding-bottom: 60px;
    }
    .form__prompt {
        line-height: 22px;
        margin-top: 25px;
    }


    .page-snippets, .page {
        width: 95%;
        margin: auto;
    }

    .colors-wrapper {
        display: none;
    }


    .texts__title {
        padding-bottom: 25px;
    }


    .buttons {
        padding-top: 40px;
    }
    .buttons__row:not(:last-child) {
        padding-bottom: 50px !important;
    }
    .buttons__row .button:not(:first-child) {
        margin-top: 20px;
    }
    .buttons .button {
        width: 100%;
    }
    .buttons-small {
        display: none !important;
    }

    .buttons .button {
        margin-right: 0;
        font-size: 15px;
    }



    .checkboxes_standard {
        margin-top: 0;
        margin-bottom: 52px;
    }

    .label__text {
        display: none;
    }

    .checkboxes__1 {
        max-width: 100%;
    }
    .checkboxes__1 .checkbox {
        margin-bottom: 31px;
    }
    .checkboxes__2 .checkboxes__line {
        margin-right: 31px;
    }

    .map {
        display: none;
    }




    h1, h2 {
        font-size: 28px;
        line-height: 34px;
    }
    h3 {
        font-size: 24px;
        line-height: 26px;
    }
    h4 {
        font-size: 20px;
    }
    h5, h6, .text {
        font-size: 15px;
        line-height: 22px;
    }

    .form__title {
        font-size: 32px; 
        line-height: 42px;
    }

    .selected-texts__item {
        width: 100%;
    }
    .selected-texts__item_bold {
        padding: 0 27px 0 36px;
        font-weight: 600;
    }



    .selected-texts {
        padding-top: 22px;
    }


    .lists_standard {
        margin-top: 0;
    }
    .lists-2 {
        display: none !important;
    }




    .links {
        padding-top: 33px;
    }
    .links .links__item:first-child {
        margin-bottom: 4px;
    }


    .slick-slider {
        margin-bottom: 90px !important;
    }


    .dark-form {
        margin-top: 56px;
        margin-bottom: 140px;
    }


    .pesontedan label {
        padding-left: 10px;
    }
    .pesontedan-content {
        padding-left: 10px !important;
    }
    .pesontedan__text {
        padding-right: 20px;
    }





    
    .form__number {
        width: 290px;
        height: 63px;
        padding: 0;
        line-height: 55px;
    }
    .form__undertext {
        margin-left: 0;
        margin-right: 0;
    }


    
    .dropwdown-lists .bg_light.bg_light-lists {
        padding-bottom: 213px !important;
    }
    .dropdown {
        width: 100% !important;
    }


    .alert__block {
        margin-bottom: 0px;
    }
    .alert__item {
        margin-bottom: 20px !important;
    }


}

@media screen and (max-width: 350px) {
    .form__number {
        width: 100%;
    }
    .form__main-wrapper {
        width: 90%;
    }


    
}