.table-wrapper-1 {
    margin-top: 118px;
    margin-bottom: 108px;
}
.table-wrapper-2 {
    padding: 53px 0 80px 0;
    background: url('../../img/dark_bg.png') center no-repeat;
}
.table, .table__title {
    width: 969px;
    border: none;
}
.table__title {
    margin-bottom: 15px !important;
}
.table td {
    line-height: 70px;
    height: 70px;
    padding-left: 19px;
   
}
.thead td {
    padding-left: 22px;
   
}
.table td {
    border-width: 0;
}
.table td:not(:last-child) {
    border-right-width: 3px;
}
.table_red tr:nth-child(2n+2) {
    background-color: #f0f0f0;
}
.table_white tr:nth-child(2n+2) {
    background: url('../../img/table_white-bg.png') center;
}

.table__col:nth-child(1) {
    width: 525px;
}
.table__col:nth-child(2) {
    width: 197px;
}
.thead_red {
    background-color: #dd1781;
}
.thead td {
    border-width: 0 !important;
}


.tbody {
    border: none;
    outline: 3px solid;
    outline-offset: -3px;
}
.tbody_red {
    outline-color: #dd1781;
}
.tbody_white {
    outline-color: #fff;
}
.tbody_white tr:nth-child(2n+1) {
    background: url('../../img/bg_white-bg.png') center;
}


.pagination-wrapper {
    padding-top: 86px;
}
.pagination_wrapper {
    padding: 0 41px 0 41px;
}
.pagination__page {
    line-height: 50px;
    font-size: 20px;
    color: #3b91e6;
    width: 50px;
    height: 50px;
    opacity: .5;
}
.paginations__arrows {
    font-size: 13px;
    width: 35px;
    height: 35px;
    background-color: #3b91e6;
    opacity: 0.25;
    cursor: pointer;
    color: #fff;
}
.paginations__arrows:hover {
    opacity: 1;
}
a.pagination__page:not(:last-child) {
    margin-right: 20px;
}
.pagination__page_active {
    opacity: 1;
    background-color: #e6eff8;
}
.pagination__number {
    border-bottom: 2px dotted #3b91e6;
}
.pagination__dots {
    color: transparent;
}

.table-wrapper-1, .table-wrapper_1 {
    justify-content: center;

}
