.bg_light {
    background-color: #f3f3f3;
    padding-top: 50px;
    padding-bottom: 151px;
    padding: 50px 98px 151px 98px;
}
.form__title_light {
    padding-bottom: 35px;
}
.form__undertitle_light {
    padding-bottom: 73px;
}
.form__message {
    margin-left: 7.5px;
    resize: none;
}
.forms-wrapper {
    margin-right: 7.5px;
}
.form__item_exp {
    height: 44px;
}
.input_border {
    border: 1px solid #e6e6e6 !important;
}
.input_border:focus {
    border: 1px solid rgba(33, 33, 33, 0.42) !important;
}
.form__item_light {
    border-color: #e6e6e6;
}
.form__item:focus {
    border: 1px solid rgba(33, 33, 33, 0.42);
}
.form__button_light {
    margin-top: 10px;
}
.form__prompt {
    font-size: 14px;
    line-height: 62px;
    color: #999999;
}






.bg-light_lists {
    padding-top: 39px;
    padding-bottom: 327px;
    margin-top: 23px !important;
    margin-bottom: 60px !important;
}
.bg-light_elements {
    padding: 90px 50px;
}
.dropdown {
    width: 45%;
}
.dropdown__button_standard {
    height: 40px;
    padding-left: 16px;
}
.dropdown__button_standard::after {
    display: none;
}
.dropdown__button_standard::before {
    position: absolute;
    font-family: 'icomoon' !important;
    content: '\e901';
    right: 13px;
    transform: rotateX(180deg);
    font-size: 14px;
}
.dropdown__button_standard,
.dropdown__menu_standard {
    width: 100%;
}
.dropdown__button, .dropdown__menu {
    background: #fff;
    cursor: pointer;
    border: none;
    color: #999999;
    border-radius: 0;
}
.dropdown__menu {
    padding: 0;
}
.dropdown__button:focus,
.dropdown__button:hover {
    outline: 0;
    color: $gray;
}
.dropdown-item {
    line-height: 50px;  
}





.alert__block {
    margin-top: 80px;
    margin-bottom: 100px;
}
.alert__title {
    margin-bottom: 23px;
}
.alert__item {
    overflow: hidden;
    margin-left: -5px;
    margin-right: -5px;
    padding: 40px 40px 27px 40px;
}
.alert__success {
    background-color: #e6eef8;
}
.alert__error {
    background-color: #f5e0e9;
}
