.bg_gray {
    background-color: $gray;
}
.header_style {
    padding: 15px 0;
}


.col-xl_1 {
    width: 95px;
    height: 100%;
}

.item_number {
    margin-top: 10px;
}
.item_show {
    margin-top: 36px;
}

.select_standard {
    cursor: pointer;
}
.select__text {
    padding-left: 14px;
}
.select__chevron {
    padding: 0 10px 0 15px;
}



.blog_bg {
    background: url("../../img/blog_background.png") center no-repeat;
    height: 208px;
    vertical-align: top;
}
.blog__nav {
    background: url("../../img/blog__nav_bg.png") left bottom no-repeat;
    background-position-x: -122px;
    overflow: hidden;
}
.blog__nav_style {
    padding: 41px 0 0 0;
}
.blog__title {
    padding-top: 21px;
    padding-bottom: 32px;
}

.map__item_nav:not(:last-child) {
    margin-right: 28px;
}
.map__arrow {
    margin-right: 9px;
}