.slider_standard {
    margin-top: 52px;
}

.slider__item {
    min-height: 399px;
}
.slider__item_blue {
    background-color: #c4ddf7;
}
.slider__item_pink {
    background-color: $pink;
}
.slider__item_gradient {
    background-image: $blueToPurple;
}
.slider__item_gray {
    background-color: $gray;
}



.slick-slider {
    margin-bottom: calc(46px + 94px) !important;
    .slick-arrow {
        width: 35px;
        height: 35px;
    }
    .slick-arrow:hover::after,
    .slick-arrow:focus::after {
        opacity: 1;
    }
    .slick-arrow::after {
        opacity: .25;
    }
    .slick-arrow::before {
        color: $blue;
        font-size: 35px;
    }
    .slick-prev {
        left: -73px;
    }
    .slick-next {
        right: -73px;
    }

    .slick-arrow_cloned {
        top: calc(100% + 22px / 2 + 22px);
    }
    .slick-next_clonned {
        right: calc(50% - 35px - 89px);
    }
    .slick-prev_clonned {
        left: calc(50% - 35px - 89px);
    }

    .slick-dots {
        height: 35px;
        bottom: calc(-29px - 22px);
        li button:before {
            color: $blue;
            font-size: 12px;
        }
        .slick-active button:before {
            opacity: 1 !important;
            color: $blue;
        }
    }


}



