$pink:#dc107e;
$brightGray: #e6eef8;
$blue: #3b91e6;
$gray: #48444b;
$primaryText: #343037;
$blueToPurple: linear-gradient(to right, #3b91e6 0%, #9d44b5 70%, #ffffff);
$pinkToWhite: linear-gradient(to right, #b5446e30 70%, #ffffff 100%);
$pinkToPurple: linear-gradient(to right, #b5446e 0%, #9d44b5 100%);

body, html {
    font-family: "Open Sans";
    overflow-x: hidden !important;
}
li, ol {
    padding: 0;
    margin: 0;
}
li {
    list-style-type: none;
}
h1, h2, h3, h4, h5, h6, ul {
    margin: 0;
    padding: 0;
}
label {
    margin: 0;
}
h1 {
    font-size: 56px;
    line-height: 64px;
}
h2 {
    font-size: 42px;
    line-height: 48px;
}
h3 {
    font-size: 36px;
    line-height: 44px;
}
h4 {
    font-size: 28px;
    line-height: 36px;
}
h5 {
    font-size: 22px;
    line-height: 30px;
}



.text, h6 {
    line-height: 24px;
    font-size: 17px;
    margin-bottom: 0;
    font-style: normal;
}
.text_xs {
    font-size: 14px;
}
.text_s {
    line-height: 57px;
    font-size: 13px;
    font-weight: 300;
}
.text_m {
    font-size: 15px;
    line-height: 22px;
}
.text_color_white {
    color: #ffffff;
}
.text_color_primary {
    color: $primaryText;
}
.text_color_pink {
    color: #dd1781;
}
.text_weight_300 {
    font-weight: 300;
}
.text_weight_400 {
    font-weight: 400;
    font-style: normal;
}
.text_weight_600 {
    font-weight: 600;
    font-style: normal;
}
.text_weight_700, h3, h4, h5, h6 {
    font-weight: 700;
    font-style: normal;
}
.text_weight_800, h1, h2 {
    font-weight: 800;
    font-style: normal;
}
.text_uppercase {
    text-transform: uppercase;
}
.text_selected_gradient-1 {
    background-image: $blueToPurple;
}
.text_selected_gradient-2 {
    background-image: $pinkToWhite;
}

.border_red {
    border: $pink solid 4px;
}

.link {
    color: $blue;
    position: relative;
}
.link:visited {
    color: #78b5f0;
}
.link_border {
    border-bottom: 1px solid $blue;
}
.link_border:visited {
    border-bottom: 1px solid #78b5f0;
}
.link_decoration_none:hover {
    text-decoration: none;
}


.chevron {
    font-size: 8px;
}


.hover {
    transition: .3s;
}
.hover_pink:hover {
    background-color: $pink;
}
.hover__link_white:hover {
    color: #ffffff;
}


.list {
    padding-left: 25px;
}


.button {
    width: 320px;
    height: 50px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.4);
    background-color: $pink;
    font-family: 'Open Sans';
    border: 3px solid #dc107e;
    outline: 0;
    position: relative;
    font-size: 18px;
    cursor: pointer;
}
.button:hover {
    background-color: #d92588;
    color: #fff;
}
.button:focus {
    outline: 0;
}
.button:active {
    top: 1px;
}
.button_hovered {
    background-color: #d92588;
}
.button_transparent {
    background-color: #fff;
    color: $pink
}
.button_small {
    width: 210px;
    height: 40px;
    font-size: 15px;
}
.button_rounded {
    border-radius: 25px;
}


.ul__item {
    position: relative;
    padding-left: 25px;
}
.ul__item:before {
    content: '';
    position: absolute;
    background-image: url('../../img/svg/ul_check.svg');
    height: 20px;
    width: 20px;
    top: 3px;
    left: 0;
}


.ol {
    counter-reset: myCounter;
}
.ol__item {
    list-style: none;
    position: relative;
}
.ol__item:before {
    counter-increment: myCounter;
    content:counter(myCounter) '.';
    position: absolute;
    top: 1px;
    left: 0;
    color: $blue;
    font-weight: 700;
}



.bg_white {
    background-color: #fff;
}

.checkbox {
    display: block;
    position: relative;
    padding-left: 36px;
    cursor: pointer;
    font-size: 15px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    display: inline-block !important;
    max-width: 230px;
}
  

/* Hide the browser's default checkbox */
.checkbox input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

/* Create a custom checkbox */
.checkmark {
    position: absolute;
    top: -1px;
    left: 0;
    height: 23px;
    width: 23px;
    border: 2px solid black;
    border-radius: 2px;
}


/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the checkmark when checked */
.checkbox input:checked ~ .checkmark:after {
    display: block;
}

/* Style the checkmark/indicator */
.checkbox .checkmark:after {
    left: 8px;
    top: 4px;
    width: 5px;
    height: 10px;
    border: solid $pink;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}


.checkmark_rounded {
    border-radius: 50%;
}
.checkbox_rounded .checkmark_rounded::after {
    border: none;
    width: 9px;
    height: 9px;
    background-color: $pink;
    border-radius: 50%;
    left: calc(50% - 4.5px);
    top: calc(50% - 4.5px);
}

.checkmark_white {
    border-color: #ffffff;
} 
.checkbox_white .checkmark_white::after {
    border-color: #fff;
}
.checkmark_rounded-white::after {
    background-color: #fff !important;
}