
.pesontedan {
    position: relative;
    width: 100%;
    overflow: hidden;
    border: 1px solid rgba(212, 212, 212, 0.45);
}
.pesontedan_hover {
    border-left: 4px solid $pink;
}
.pesontedan__title {
    line-height: 76px;
    text-shadow: none;
}
.pesontedan input {
    position: absolute;
    opacity: 0;
    z-index: -1;
}

.pesontedan label {
    position: relative;
    display: block;
    padding: 0 0 0 31px;
    background: #fff;
    line-height: 3;
    cursor: pointer;
    text-shadow: 0 1px 0 #333131;
    font-size: 17px;
    height: 76px;
}

.pesontedan-content {
    max-height: 0;
    overflow: hidden;
    background: #ffffff;;
    transition: max-height .40s;
    font-size: 14px;
    text-shadow: 0 1px 0 #292727;
    padding: 0 0 0 31px;
    text-shadow: none;
}

.pesontedan-content {
    background: #fff;
}
.pesontedan__title {
    height: 76px;
    padding-right: 35px;
}
.pesontedan__text {
    padding: 10px 70px 30px 0;
}
/* :checked */
.pesontedan input:checked ~ .pesontedan-content {
    max-height: 100vh;
}

/* Icon */
.pesontedan label::after {
    position: absolute;
    right: 0;
    top: 0;
    display: block;
    width: 76px;
    height: 76px;
    line-height: 4.5;
    text-align: center;
    -webkit-transition: all .40s;
    -o-transition: all .40s;
    transition: all .40s;
}
.pesontedan input[type=checkbox] + label::after {
    content: "";
    background: url('../../img/svg/context_arrow.svg') no-repeat center;
    transform: rotate(180deg);
}
.pesontedan input[type=radio] + label::after {
    content: "";
}
.pesontedan input[type=checkbox]:checked + label::after {
    transform: rotate(360deg);
}
.pesontedan input[type=radio]:checked + label::after {
    transform: rotateX(180deg);
}