.dark-form_standard {
    margin-top: 120px;
    background: url('../../img/dark_bg.png') center no-repeat;
    margin-bottom: 82px;
}

.form__title_dark {
    padding: 97px 0 48px 0;
}
.form__number_dark {
    margin: 0 -35px !important;
}
.form__undertext_dark {
    margin: 30px -35px 67px -35px;
}
.form__checkbox_dark {
    font-size: 14px;
    max-width: 280px;
    margin: 40px 0 12px 0;
}
.form__button {
    width: 236px;
}
.form__button_dark {
    margin-bottom: 89px;
}
.form__undertext_red {
    color: #dd1781;
    font-style: normal;
}
.form__undertext_red:hover {
    color: #dd1781;
}
.form__number {
    line-height: 32px;
    padding: 37px 42px 34px 40px; 
    width: 420px;
    height: 111px; 
    margin: auto !important;
}
.form__title {
    font-size: 46px;
    line-height: 48px;
}
.form__undertext {
    font-size: 20px;
}

.form__item-wrapper {
    position: relative;
}
.form__item-wrapper:not(:last-child) {
    margin-bottom: 20px;
}
.form__item-wrapper::after {
    font-family: 'icomoon' !important;
    content: '';
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translate(0, -50%);
    color: #21212155;
}
.form__item:focus::after {
    color: #212121;
}
#user::after {
    content: '\e905';
}
#tel::after {
    content: '\e902';
}
#email::after {
    content: '\e906';
}
.form__item {
    border: 1px solid #212121;
    line-height: 40px;
    padding-left: 14px;
}
.form__item:focus {
    opacity: 1;
    outline: 0;
}
.form__item::placeholder {
    font-size: 17px;
}
.form__item:focus::placeholder {
    color: #212121;
}


.undercheckbox-text {
    font-size: 14px;
    margin-bottom: 53px;
}